// Interactive Playground
export const InteractivePlayground: Story = {
  render: () => {
    const [dragEvents, setDragEvents] = useState<string[]>([])
    const [hoverCount, setHoverCount] = useState(0)
    
    const addEvent = (event: string) => {
      setDragEvents(prev => [...prev.slice(-4), `${new Date().toLocaleTimeString()}: ${event}`])
    }
    
    return (
      <div className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <LiquidCard
            variant="frosted"
            draggable
            magneticEdges
            magneticStrength={0.5}
            className="p-6 min-h-[200px]"
            onDragStart={() => addEvent('Drag started')}
            onDrag={(_, pos) => addEvent(`Dragging (${Math.round(pos.x)}, ${Math.round(pos.y)})`)}
            onDragEnd={() => addEvent('Drag ended')}
            onHover={(hovered) => {
              if (hovered) setHoverCount(c => c + 1)
              addEvent(hovered ? 'Hover enter' : 'Hover leave')
            }}
          >
            <h4 className="text-lg font-semibold text-white mb-2">
              Interactive Playground
            </h4>
            <p className="text-gray-300 text-sm mb-4">
              Drag me around and watch the event log!
            </p>
            <div className="text-xs text-blue-200">
              Hovers: {hoverCount}
            </div>
          </LiquidCard>
          
          <div className="space-y-4">
            <h5 className="text-lg font-semibold text-white">Event Log</h5>
            <div className="bg-black/30 rounded-lg p-4 font-mono text-xs">
              {dragEvents.length === 0 ? (
                <div className="text-gray-500">No events yet...</div>
              ) : (
                dragEvents.map((event, i) => (
                  <div key={i} className="text-gray-300">{event}</div>
                ))
              )}
            </div>
          </div>
        </div>
      </div>
    )
  },
  parameters: {
    docs: {
      description: {
        story: 'Interactive playground showing all drag and hover events with real-time feedback.'
      }
    }
  }
}

